<!--suppress JSUnresolvedReference -->
<script>
import storage from "@/utils/storage";

export default {
  name: "GroupHeader",
  data() {
    return {
      lang: 'tc',
      langList: [
        {value: 'tc', name: '繁'},
        {value: 'sc', name: '简'},
        {value: 'en', name: 'ENG'}
      ],
      showNav: false
    }
  },
  mounted() {
    this.selectLangHandle(storage.getLanguage());
  },
  methods: {
    selectLangHandle(type) {
      this.lang = type;
      this.$i18n.locale = type;
      storage.setLanguage(type);
    }
  }
}
</script>

<template>
  <div class="w-full  border-b border-gray-400 absolute top-0 left-0 z-50">
    <div class="container mx-auto h-20 flex items-center justify-between xs:px-10 px-3">
      <div class="flex items-center">
        <img src="@/assets/visit.png" alt="logo" class="h-16 hidden sm:block"/>
        <ul class="ml-10 text-lg text-white hidden sm:block">
          <li class="inline-block px-4">
            <router-link to="/">{{ $t("group.header.nav.home") }}</router-link>
          </li>
          <li class="inline-block px-4">
            <router-link to="/about">{{ $t("group.header.nav.investor") }}</router-link>
          </li>
          <li class="inline-block px-4">
            <router-link to="/contact">{{ $t("group.header.nav.about") }}</router-link>
          </li>
        </ul>
        <div class="sm:hidden">
          <i class="el-icon-more  block text-white text-3xl" @click="showNav = !showNav"></i>
        </div>
      </div>
      <div class="flex text-white">
        <div class="px-2.5 mx-1 cursor-pointer" v-for="(item,index) in langList" :key="item.value"
             :class="{'language-selector': lang === item.value}"
             @click="selectLangHandle(item.value)">
          {{ item.name }}
        </div>
      </div>
    </div>
    <el-drawer
        title=""
        modal-append-to-body
        append-to-body
        size="60%"
        :visible.sync="showNav"
        direction="ltr">
      <ul class="text-lg flex flex-col">
        <li class="px-4 leading-5 h-5 text-xl py-5 hover:bg-coffeePink transition-all flex items-center">
          <router-link to="/">{{ $t("group.header.nav.home") }}</router-link>
        </li>
        <li class="px-4 leading-5 h-5 text-xl py-5 hover:bg-coffeePink transition-all flex items-center">
          <router-link to="/about">{{ $t("group.header.nav.investor") }}</router-link>
        </li>
        <li class="px-4 leading-5 h-5 text-xl py-5 hover:bg-coffeePink transition-all flex items-center">
          <router-link to="/contact">{{ $t("group.header.nav.about") }}</router-link>
        </li>
      </ul>
    </el-drawer>
  </div>
</template>

<style scoped>
.language-selector {
  background-color: orange;
  color: black;
  border-radius: 5px;
}
</style>