</template>
<script>
import GroupHeader from '@/components/group/GroupHeader.vue'


export default {
  name: 'Home',
  components: {
    GroupHeader
  },
  mounted() {
    window.addEventListener('scroll', this.handleScrollbox)
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScrollbox)
  },
  data() {
    return {
      contactForm: {
        name: "",
        email: "",
        phone: "",
        desc: ""
      },
      rules: {},
      showProduct: false,
      currentScroll: 0,
      showDesc1: false,
      showDesc2: false
    }
  },
  methods: {
    handleScrollbox() {
      this.currentScroll = window.pageYOffset //表示当前滚动的位置
      console.log(this.currentScroll)
      if (this.currentScroll >= 550) {
        this.showProduct = true;
      }
      if (this.currentScroll >= 1100) {
        this.showDesc1 = true;
      }
      if (this.currentScroll >= 1500) {
        this.showDesc2 = true;
      }
    },
    async onSubmit() {
      const baseRules = {
        name: [
          {required: true, message: this.$t('group.home.four.rules.name')}
        ],
        phone: [
          {required: true, message: this.$t('group.home.four.rules.phone')}
        ],
        email: [
          {required: true, message: this.$t('group.home.four.rules.email')}
        ],
        desc: [
          {required: true, message: this.$t('group.home.four.rules.content')}
        ]
      }
      this.rules = baseRules;
      await this.$nextTick();
      const valid = await this.$refs.contactForm.validate();
      if (valid) {
        this.$message({
          message: '提交成功',
          type: 'success',
          onClose: () => {
            this.$refs.contactForm.resetFields();
          }
        })
      }
    }
  }
}
</script>
